<!-- 分辨率 900*573 -->
<template>
  <div class="kanban_page">
    <div class="title">综合指标</div>
    <div class="device_effciency">
      <div class="kanban_module_title">设备效率</div>
      <div class="effciency_charts" ref="effciency"></div>
    </div>
    <div class="device_state">
      <div class="kanban_module_title">今日设备状态</div>
      <div class="state_charts" ref="state"></div>
    </div>
  </div>
</template>

<script>
// import $ from 'jquery';
import { fontSize } from '@/utils/common';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';
import { STATUS_CODE } from '@/utils/constantsFile';
import {
  deviceRealLiveGetDeviceStateReportStatistics,
  deviceReportGetDeviceDayUtilization,
  getServerTime
} from '@/api/kanban';
export default {
  name: 'distribute',
  data() {
    return {
      kanbanConfig: {},
      devLbIndex: 0,
      devList: [],
      current: 1,
      size: 6,
      currentTime: 0,
      dataTime: 0
    };
  },
  created() {
    document.documentElement.style.fontSize = fontSize() + 'px';
    window.onresize = () => {
      document.documentElement.style.fontSize = fontSize() + 'px';
    };
    if (KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)]) {
      this.kanbanConfig =
        KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];
    }
    // this.currentTime = new Date($.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date')).getTime();
  },
  async mounted() {
    // try {
    //   const res = await getServerTime({}, {});
    //   this.currentTime = res.rows;
    //   console.log(res);
    // } catch (error) {
    //   console.log(error);
    // }
    this.getDataTime();
    this.draweffciencyChart();
    this.getDeviceData();
    this.getDeviceState();
    this.timer = setInterval(() => {
      // this.getDataTime();
      this.draweffciencyChart();
      this.getDeviceData();
      this.getDeviceState();
    }, this.kanbanConfig?.time || 15000);
    setInterval(() => {
      this.getDataTime();
    }, 30000);
  },
  methods: {
    getSearchToday(num) {
      var date = new Date(this.dataTime - (60 * 60 * 1000 * 24) * num);
      var y = date.getFullYear(); // 获取年份
      var m = date.getMonth() + 1; // 获取月份
      var d = date.getDate(); // 获取当月最后一日
      m = m < 10 ? '0' + m : m; // 月份补 0
      d = d < 10 ? '0' + d : d; // 日数补 0
      return [y, m, d].join('-');
    },
    getTodayFirstDay() {
      const time = new Date(this.dataTime);
      var y = time.getFullYear(); // 获取年份
      var m = time.getMonth() + 1; // 获取月份
      var d = time.getDate(); // 获取当月最后一日
      m = m < 10 ? '0' + m : m; // 月份补 0
      d = d < 10 ? '0' + d : d; // 日数补 0

      return [y, m, d].join('-') + ' 00:00:00';
    },
    getTodayLastDay() {
      const time = new Date(this.dataTime);
      var y = time.getFullYear(); // 获取年份
      var m = time.getMonth() + 1; // 获取月份
      var d = time.getDate(); // 获取当月最后一日
      m = m < 10 ? '0' + m : m; // 月份补 0
      d = d < 10 ? '0' + d : d; // 日数补 0

      return [y, m, d].join('-') + ' 23:59:59';
    },
    hexToRgba(hex, opacity) {
      let rgbaColor = '';
      const reg = /^#[\da-f]{6}$/i;
      if (reg.test(hex)) {
        rgbaColor = `rgba(${parseInt('0x' + hex.slice(1, 3))},${parseInt(
          '0x' + hex.slice(3, 5)
        )},${parseInt('0x' + hex.slice(5, 7))},${opacity})`;
      }
      return rgbaColor;
    },
    // 设备效率图
    draweffciencyChart(sbxl, pjxl, time, devName) {
      const option = {
        grid: {
          left: '2%',
          right: '2%',
          bottom: '10%',
          top: '15%',
          containLabel: true
        },
        title: {
          text: devName,
          padding: fontSize(10),
          left: fontSize(30),
          textStyle: {
            color: '#fff',
            fontSize: fontSize(18)
          }
        },
        tooltip: {
          show: true,
          trigger: 'axis'
        },
        // color: ['', '#0C49C0'],
        legend: {
          show: true,
          right: fontSize(40),
          icon: 'rect',
          itemWidth: fontSize(30),
          itemHeight: fontSize(10),
          textStyle: {
            fontSize: fontSize(18),
            color: '#ffffff'
          }
        },
        xAxis: {
          name: '',
          type: 'category',
          boundaryGap: false,
          data: time,
          axisLabel: {
            fontSize: fontSize(18)
          },
          axisTick: {
            show: false
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: '#2077A3'
            }
          },
          nameTextStyle: {
            fontSize: fontSize(18)
          }
        },
        yAxis: {
          name: '',
          type: 'value',
          axisLine: {
            show: true,
            lineStyle: {
              color: '#2077A3'
            }
          },
          axisTick: {
            show: false
          },
          splitLine: {
            show: false
          },
          axisLabel: {
            fontSize: fontSize(18)
          },
          nameTextStyle: {
            fontSize: fontSize(18)
          }
        },
        textStyle: {
          color: '#929EE6'
        },
        series: [
          {
            name: '平均效率',
            type: 'line',
            zlevel: 1,
            showSymbol: false,
            color: '#36CE9E',
            lineStyle: {
              normal: {
                color: '#36CE9E',
                shadowBlur: 3
              }
            },
            emphasis: {
              focus: 'series'
            },
            data: pjxl
          },
          {
            name: '设备效率',
            type: 'line',
            zlevel: 1,
            smooth: true,
            lineStyle: {
              width: 0
            },
            color: '#0C49C0',
            showSymbol: false,
            areaStyle: {
              opacity: 0.6,
              color: '#0C49C0'
            },
            emphasis: {
              focus: 'series'
            },
            data: sbxl
          }
        ]
      };
      const chart = this.$echarts.init(this.$refs.effciency);
      chart.setOption(option);
    },
    // 今日设备状态图
    drawStateChart(run, free, noline, warn, name) {
      const dataArr = {
        xdata: name,
        result: [
          { name: '运行', data: run },
          { name: '告警', data: warn },
          { name: '空闲', data: free },
          { name: '离线', data: noline }
        ]
      };
      const dealTotal = (cut, index) => {
        let total = 0;
        dataArr.result.slice(0, cut + 1).forEach(item => {
          total += item.data[index];
        });
        return total;
      };
      // 头部菱形
      const diamondData = dataArr.result.reduce((pre, cur, index) => {
        pre[index] = cur.data.map(
          // (el, id) => el + (pre[index - 1] ? pre[index - 1][id] : 0)
          (el, id) => {
            if (index === 0) {
              return el;
            } else if (el) {
              return dealTotal(index, id);
            } else {
              return el;
            }
          }
        );
        return pre;
      }, []);
      // color
      const color = [
        [
          { offset: 0, color: '#0C66FF' },
          { offset: 1, color: '#18C5FF' }
        ],
        [
          { offset: 0, color: '#FF0C0C' },
          { offset: 1, color: '#FB586B' }
        ],
        [
          { offset: 0, color: '#FFE00C' },
          { offset: 1, color: '#FDE576' }
        ],
        [
          { offset: 0, color: '#696969' },
          { offset: 1, color: '#C1C1C1' }
        ]
      ];
      // series
      const series = dataArr.result.reduce((p, c, i, array) => {
        p.push(
          {
            z: i + 1,
            stack: '总量',
            type: 'bar',
            name: c.name,
            barGap: '-100%',
            barWidth: 30,
            data: c.data,
            itemStyle: {
              color: {
                type: 'linear',
                x: 0,
                x2: 1,
                y: 0,
                y2: 0,
                colorStops: color[i]
              }
            }
          },
          {
            z: i + 10,
            type: 'pictorialBar',
            symbolPosition: 'end',
            symbol: 'diamond',
            symbolOffset: [0, '-50%'],
            symbolSize: [30, 10],
            data: diamondData[i],
            itemStyle: {
              color: {
                type: 'linear',
                x: 0,
                x2: 1,
                y: 0,
                y2: 0,
                colorStops: color[i]
              }
            },
            tooltip: { show: false }
          }
        );

        // 是否最后一个了？
        if (p.length === array.length * 2) {
          p.push({
            z: array.length * 2,
            type: 'pictorialBar',
            symbolPosition: 'start',
            data: dataArr.result[0].data,
            symbol: 'diamond',
            symbolOffset: ['0%', '50%'],
            symbolSize: [30, 10],
            itemStyle: {
              color: {
                type: 'linear',
                x: 0,
                x2: 1,
                y: 0,
                y2: 0,
                colorStops: color[0]
              }
            },
            tooltip: { show: false }
          });
          return p;
        }

        return p;
      }, []);
      const option = {
        grid: {
          left: '2%',
          right: '2%',
          bottom: '10%',
          top: '15%',
          containLabel: true
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: dataArr.result.map(item => item.name),
          textStyle: { fontSize: fontSize(18), color: '#fff' },
          itemWidth: fontSize(30),
          itemHeight: fontSize(15),
          itemGap: fontSize(15),
          right: fontSize(30),
          // 禁止点击
          selectedMode: false
        },
        xAxis: {
          axisTick: { show: true },
          axisLine: {
            show: true,
            lineStyle: {
              color: '#2077A3'
            }
          },
          axisLabel: { textStyle: { fontSize: fontSize(18), color: '#fff' } },
          data: dataArr.xdata
        },
        yAxis: {
          axisLine: {
            show: true,
            lineStyle: {
              color: '#2077A3'
            }
          },
          axisTick: {
            show: false
          },
          splitLine: {
            show: false
          },
          axisLabel: { textStyle: { fontSize: fontSize(18), color: '#fff' } }
        },
        series
      };
      const chart = this.$echarts.init(this.$refs.state);
      chart.setOption(option);
    },
    // 设备效率数据处理
    async getDeviceData() {
      if (
        this.devList.length === 0 ||
        this.devLbIndex === this.devList.length - 1
      ) {
        try {
          var condtion = {
            secretKey: this.kanbanConfig.key,
            conditionJson1: [
              {
                column: 'isProductionDevice',
                operator: 1,
                ptype: 'int',
                type: 'eq',
                value: 1
              }
            ]
          };
          const res = await deviceRealLiveGetDeviceStateReportStatistics(
            {
              current: 1,
              size: 99999,
              startTime: this.getTodayFirstDay(),
              endTime: this.getTodayLastDay()
            },
            condtion
          );
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.devList = res.rows;
            this.getDeviceTimeRecentDayUtilization();
            this.devLbIndex = 0;
          }
        } catch (error) {
          console.log(error);
        }
      } else {
        this.getDeviceTimeRecentDayUtilization();
        this.devLbIndex++;
      }
    },
    // 获取设备效率数据
    async getDeviceTimeRecentDayUtilization() {
      try {
        var condtion = {
          conditionJson1: [
            {
              column: 'deviceCode',
              operator: 1,
              ptype: 'string',
              type: 'eq',
              value: this.devList[this.devLbIndex].deviceCode
            }
          ],
          secretKey: this.kanbanConfig.key
        };
        // if (!localStorage.getItem('companyId')) {
        //   condtion.secretKey = this.kanbanConfig.key;
        // }
        const res = await deviceReportGetDeviceDayUtilization(
          {
            startTime: this.getSearchToday(7) + ' 00:00:00',
            endTime: this.getSearchToday(0) + ' 23:59:59'
          },
          condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          const sbxl = [];
          const pjxl = [];
          const time = [];
          const all = [];
          var numAll = 0;
          for (var i = 0; i < res.rows.length; i++) {
            // pjxl.push(Number((data.dataset * 100).toFixed(2)));
            sbxl.push(Number((res.rows[i].value * 100).toFixed(2)));
            time.push(this.formatDateThree(res.rows[i].time));
            all.push(Number((res.rows[i].value * 100).toFixed(2)));
            numAll += res.rows[i].value;
          }
          const avg = ((numAll / all.length) * 100).toFixed(2);
          for (let x = 0; x < all.length; x++) {
            pjxl.push(Number(avg));
          }
          this.draweffciencyChart(
            sbxl,
            pjxl,
            time,
            this.devList[this.devLbIndex].deviceName
          );
        }
      } catch (error) {
        console.log(error);
      }
    },
    // 获取今日设备状态数据
    async getDeviceState() {
      var condtion = {
        secretKey: this.kanbanConfig.key,
        conditionJson1: [
          {
            column: 'isProductionDevice',
            operator: 1,
            ptype: 'int',
            type: 'eq',
            value: 1
          }
        ]
      };
      try {
        const res = await deviceRealLiveGetDeviceStateReportStatistics(
          {
            current: this.current,
            size: this.size,
            startTime: this.getTodayFirstDay(),
            endTime: this.getTodayLastDay()
          },
          condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (this.current * this.size >= res.total) {
            this.current = 1;
          } else {
            this.current++;
          }

          const run = [];
          const free = [];
          const noline = [];
          const warn = [];
          const name = [];
          for (let i = 0; i < res.rows.length; i++) {
            const all =
              res.rows[i].deviceLiveReportVo.runTimeSum +
              res.rows[i].deviceLiveReportVo.idleTimeSum +
              res.rows[i].deviceLiveReportVo.offlineTimeSum +
              res.rows[i].deviceLiveReportVo.warnTimeSum;
            run.push((res.rows[i].deviceLiveReportVo.runTimeSum / all) * 100);
            noline.push(
              (res.rows[i].deviceLiveReportVo.offlineTimeSum / all) * 100
            );
            free.push((res.rows[i].deviceLiveReportVo.idleTimeSum / all) * 100);
            warn.push((res.rows[i].deviceLiveReportVo.warnTimeSum / all) * 100);
            name.push(res.rows[i].deviceName);
          }
          this.drawStateChart(run, free, noline, warn, name);
        }
      } catch (error) {
        console.log(error);
      }
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return (
        decodeURIComponent(
          (new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(
            url
          ) || ['', ''])[1].replace(/\+/g, '%20')
        ) || null
      );
    },
    formatDateThree(time) {
      let date = '';
      let D = '';
      date = new Date(time);
      const M =
        date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1;
      const d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      D = M + '-' + d;
      return D;
    },
    // 看板时间
    async getDataTime() {
      try {
        const res = await getServerTime({}, {});
        this.dataTime = res.rows;
        console.log(res);
      } catch (error) {
        console.log(error);
      }
    }

  }
};
</script>

